@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  /* font-family: 'Roboto', sans-serif; */
  font-family: 'Roboto Condensed', sans-serif;
}

.App {
  position: relative;
  background-image: url('../img/header.jpg');
  min-height: 100vh;
  overflow-x: hidden;
}

.menu {
  position: absolute;
  right: 0;
  left: 0;
  top: -40vh;
  height: 40vh;
  background-color: rgba(255, 255, 255, .3);
  border-radius: 20px;
  transition: .3s;
}

.menu.active {
  top: 0;
}

.menuButton {
  position: absolute;
  left: 50%;
  bottom: -50px;
  /* width: 40px; */
  /* height: 40px; */
  border-radius: 0 0 5px 5px;
  transform: translateX(-50%);
  background-color: rgba(255, 255, 255, .3);
  padding: 5px 15px;
  font-size: 26px;
  line-height: 40px;
  text-align: center;
  color: #fff;
}

.main {
  position: absolute;
  left: 20px;
  right: 20px;
  top: 80px;
  padding: 20px;
  background-color: rgba(255, 255, 255, .3);
  border-radius: 20px;
  overflow: hidden;
  transition: .3s;
}

.main.active {
  top: calc(80px + 40vh)
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 5vh;
  text-align: center;
  font-size: 20px;
  line-height: 5vh;
  background-color: #000;
  color: #fff;
}